import React from "react"
import { Box, Heading } from "@chakra-ui/react"
import extractVersionFromPathname from "../../utils/extractVersionFromPathname"
import MenuItem from "./menuItem"

const Menu = ({ content, rootTitle, location }) => {
  const selectedApiVersion = extractVersionFromPathname(location.pathname)
  const menuPathPrefix = location.pathname.split("/").filter(x => x !== "")[
    selectedApiVersion === "" ? 0 : 1
  ]

  const absolutePath = rootTitle?.frontmatter?.title
  const sectionName = absolutePath?.replaceAll("TOC", "")

  return (
    <Box>
      <Heading fontSize={20} fontWeight={700} pb={2.5}>
        {sectionName}
      </Heading>
      <MenuItem
        items={content}
        menuPathPrefix={menuPathPrefix}
        selectedApiVersion={selectedApiVersion}
        level={0}
        location={location}
      />
    </Box>
  )
}

export default Menu
